import React, {useEffect, useRef} from 'react';
// import { Link } from "react-router-dom";
import PhoneImg from './../assets/img/phone.svg';
import phoneTooltip from './../assets/img/phone-tooltip.svg';
 import phoneHand from './../assets/img/hand.svg';
 import handText from './../assets/img/hand-text.svg';
 import craneOne from './../assets/img/crane-one.svg';
 import craneOneIcon from './../assets/img/crane-one-icon.svg';
 import craneTwo from './../assets/img/crane-two.svg';
 import cloudOne from './../assets/img/cloud-one.svg';
 import cloudTwo from './../assets/img/cloud-two.svg';
 import cloudThree from './../assets/img/cloud-three.svg';
 import gsap from "gsap";
 import { TimelineMax } from "gsap";


 export default function FirstComponent() {
    // const [name, setname] = useState('Tontole');
    const phoneRef = useRef(null);
    const handRef = useRef(null);
    const handTextRef = useRef(null);
    const phoneTooltipRef = useRef(null);
    const craneOneRef = useRef(null);
    const craneOneIconRef = useRef(null);
    const craneTwoRef = useRef(null);
    const cloudOneRef = useRef(null);
    const cloudTwoRef = useRef(null);
    const cloudThreeRef = useRef(null);
    const cloudFourRef = useRef(null);
    const cloudFiveRef = useRef(null);
    const cloudSixRef = useRef(null);


    
    const tl = new TimelineMax({paused: false, repeat:-1});
    const tl2 = new TimelineMax({paused: false, repeat:-1});
    const tl3 = new TimelineMax({paused: false, repeat:-1});
    const tl4 = new TimelineMax({paused: false, repeat:-1});
    const tl5 = new TimelineMax({paused: false, repeat:-1});
    const tl6 = new TimelineMax({paused: false, repeat:-1});
    const tl7 = new TimelineMax({paused: false, repeat:-1});


    useEffect(() => {
         tl
        .from( phoneRef.current, {duration: 1, y: 1000})
        .from( handRef.current, {duration: 1, x: 1000})
        .from( handTextRef.current, {duration: 1, x: 1000}, '-=1')
        .from( phoneTooltipRef.current, { duration: 1, autoAlpha:0},)
        .from( craneOneRef.current, { duration: 1,  y: -1000})
        .from( craneOneIconRef.current, { duration: 1, y: -1000}, '-=1')
        .from( craneTwoRef.current, {duration:1, transformStyle: 'preserve-3d', transform: 'rotateY(450deg)'})
        .to( craneOneRef.current, { duration: 7, y: -1000}, '-=1')
        .to( handRef.current, { duration: 5, x: 1000}, '-=8.5')
        ;
        tl2.fromTo(cloudOneRef.current, {xPercent: -80}, {duration: 15, xPercent: 220});
        tl3.fromTo(cloudTwoRef.current, {xPercent: -80}, {duration: 25, xPercent: 220});
        tl4.fromTo(cloudThreeRef.current, {xPercent: -80}, {duration: 35, xPercent: 220});
        tl5.fromTo(cloudFourRef.current, {xPercent: -200}, {duration: 100, xPercent: 220},'=2');
        tl6.fromTo(cloudFiveRef.current, {xPercent: -200}, {duration: 240, xPercent: 220}, '=5');
        tl7.fromTo(cloudSixRef.current, {xPercent: -200}, {duration: 380, xPercent: 220}, '=7');

        // Update the document title using the browser API
      });

    return (
        <div id="animteSection">
        <div className="phone-wrapper" >
            <img src={PhoneImg} ref={phoneRef} className="phone-img" id="phone-img" alt="phone-img" />    
            <img src={phoneTooltip} ref={phoneTooltipRef}  className="phone-tooltip" alt="phone-img" />    
            <img src={phoneHand} ref={handRef} className="phone-hand" alt="phone-img" /> 
            <img src={handText} ref={handTextRef} className="phone-hand-text" alt="phone-img" /> 
            <img src={craneOne} ref={craneOneRef} className="phone-crane" alt="phone-img" /> 
            <img src={craneOneIcon}  ref={craneOneIconRef} className="phone-crane-icon" alt="phone-img" /> 
            <img src={craneTwo} ref={craneTwoRef} className="phone-crane-two" alt="phone-img" /> 
            <img src={cloudOne} ref={cloudOneRef} className="phone-cloud-one" alt="phone-img" /> 
            <img src={cloudOne} ref={cloudTwoRef} className="phone-cloud-two" alt="phone-img" /> 
            <img src={cloudOne} ref={cloudThreeRef} className="phone-cloud-three" alt="phone-img" /> 
            <img src={cloudOne} ref={cloudFourRef} className="phone-cloud-four" alt="phone-img" /> 
            <img src={cloudOne} ref={cloudFiveRef} className="phone-cloud-five" alt="phone-img" /> 
            <img src={cloudOne} ref={cloudSixRef} className="phone-cloud-six" alt="phone-img" /> 


        </div>
        <div className="animation-bg"></div>
        </div>
        
    );
}

